<template>
    <div class="item-loot-block position-relative mx-4"
         ref="parent">
        <div class="position-absolute bg-white start-0 top-0 left-guide-line"
             style="width: 2px;"></div>
        <div class="position-absolute start-0 top-0"
             style="width: 16px; height:24px;"
             v-if="isChild">
            <div class="position-absolute bg-white top-0 start-0 h-50"
                 style="width: 2px;"></div>
            <div class="position-absolute bg-white top-50 start-0 w-100"
                 style="height: 2px;"></div>
        </div>
        <div class="ps-4 d-flex align-items-center">
            <div class="border-0"
                 style="width: 14px;cursor: pointer"
                 v-on:click="expend()">
                <template v-if="this.canBeFoundLootTable() && !this.isLootBucket()">
                    <span v-show="!showChild">
                        <i class="far fa-plus-square"></i>
                    </span>
                    <span v-show="showChild">
                        <i class="far fa-minus-square"></i>
                    </span>
                </template>
            </div>
            <div class="mx-1"
                 style="font-family: monospace;white-space: pre;">
                <span style="color: aqua"
                      v-text="'[' + probPercent + ']'"></span>{{ search }} ({{ this.canBeFoundLootTable() ? item['AND/OR'] : 'N' }})
            </div>
        </div>
        <template v-if="showChild">
            <ItemLootBlock :search="lootID"
                           :isChild="hasChild()"
                           :probValue="parseInt(itemProb['Item' + (lootIDKey + 1)])"
                           :probMaxValue="
                           itemProb['MaxRoll']"
                           :isDefaultExpend="isDefaultExpend"
                           :stop="(stop + 1)"
                           v-for="(lootID, lootIDKey) in lootIDs"
                           :key="lootIDKey"></ItemLootBlock>
        </template>
    </div>
</template>

<script>
import Big from 'big.js/big.js'
import collect from 'collect.js/dist/index'
import javelindata_loottables from '../assets/data/javelindata_loottables.json'

export default {
    props: {
        search: String,
        isChild: Boolean,
        probMaxValue: Number,
        probValue: Number,
        isDefaultExpend: Boolean,
        stop: Number,
    },
    data() {
        return {
            loottable: collect(javelindata_loottables),
            isExpend: false,

            item: {},
            itemIndex: -1,
            itemProb: {},
            itemProbIndex: -1,
            itemQty: {},
            itemQtyIndex: -1,

            lootIDs: [],

            parentDOM: {},
        }
    },
    methods: {
        hasChild() {
            return this.lootIDs.length > 0
        },
        expend() {
            this.isExpend = !this.isExpend
        },
        canBeFoundLootTable() {
            return !!this.item
        },
        isLootBucket() {
            return this.$props.search.match(/\[LBID\]/)
        },
    },
    computed: {
        probPercent: function () {
            if (this.probMaxValue == 0) {
                return '100.00%'
            }
            let probMaxValue = new Big(this.probMaxValue),
                probValue = new Big(this.probValue)

            var temp = probMaxValue.minus(probValue)
            temp = temp.div(probMaxValue)
            temp = temp.times(100)

            return temp.toFixed(2).padStart(6) + '%'
        },
        showChild: function () {
            if (this.stop > 100) {
                console.log('max stack')
                return false
            }

            return this.isExpend
        },
    },
    watch: {
        isDefaultExpend: function () {
            this.isExpend = this.isDefaultExpend
        },
    },
    mounted() {
        if (!this.isLootBucket()) {
            var searchName = this.$props.search.replace(/\[LTID\]|\[LBID\]/, '')

            this.loottable.map((item) => {
                if (item['LootTableID'] == searchName) {
                    this.item = item
                }
                if (item['LootTableID'] == searchName + '_Probs') {
                    this.itemProb = item
                }
                if (item['LootTableID'] == searchName + '_Qty') {
                    this.itemQty = item
                }
            })
        }

        if (this.canBeFoundLootTable()) {
            let itemKeys = Object.keys(this.item)
            for (let index in itemKeys) {
                var key = itemKeys[index]
                if (key.match(/Item(\d)/)) {
                    this.lootIDs.push(this.item[key])
                }
            }
        }

        if (this.isDefaultExpend) {
            this.isExpend = true
        }
    },
}
</script>

<style scoped>
.left-guide-line {
    height: 100%;
}
.item-loot-block:last-child > .left-guide-line {
    height: 0;
}
</style>